module.exports = "<table style=\"display: table; margin-bottom: 1.25rem\"><thead><tr style=\"line-height: 1.5\"><th style=\"width: 10%\">Prop Name</th><th style=\"width: 25%\">Type</th><th style=\"width: 10%\">Is Required</th><th style=\"width: 10%\">Default Value</th><th style=\"width: 45%\">Description</th></tr></thead><tbody><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">buttonAttrs</td><td style=\"padding-bottom: 1.8rem\">An array of objects structured like:<br /><pre style=\"white-space: pre-wrap\">{\n \"name\": \"shape\",\n \"value\": \"Button.propTypes\",\n \"computed\": true\n}</pre></td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">[]</td><td style=\"padding-bottom: 1.8rem\">An array of objects containing terra-button properties. A key attribute is required for each object.\nThis array is used to render buttons in the bottom section.\nExample:<code style=\"white-space: pre-wrap\">[{ text: &#39;Button 1&#39;, key: 1, size: &#39;medium&#39;, variant: &#39;action&#39;, onClick: onClickFunction}]</code></td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">customGlyph</td><td style=\"padding-bottom: 1.8rem\">node</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">undefined</td><td style=\"padding-bottom: 1.8rem\">Display a custom glyph. Overrides a variant&#39;s default glyph.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">isAlignedTop</td><td style=\"padding-bottom: 1.8rem\">bool</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">false</td><td style=\"padding-bottom: 1.8rem\">Aligns the component at the top of the container rather than &quot;centered&quot;</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">isGlyphHidden</td><td style=\"padding-bottom: 1.8rem\">bool</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">false</td><td style=\"padding-bottom: 1.8rem\">Determines if the glyph should be displayed.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">message</td><td style=\"padding-bottom: 1.8rem\">string</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">undefined</td><td style=\"padding-bottom: 1.8rem\">The descriptive text, displayed under the title.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">title</td><td style=\"padding-bottom: 1.8rem\">string</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">undefined</td><td style=\"padding-bottom: 1.8rem\">The title displayed under the glyph. Variants contain default titles that can be overriden by this prop.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">variant</td><td style=\"padding-bottom: 1.8rem\">enum</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">undefined</td><td style=\"padding-bottom: 1.8rem\">Sets the glyph and title using a pre-baked variant. One of the following: <code style=\"white-space: pre-wrap\">no-data</code>,\n<code>no-matching-results</code>, <code>not-authorized</code>, or <code>error</code></td></tr></tbody></table>"